.social {
  display: flex;
  flex-wrap: wrap;
  align-self: self-end;
  margin-left: auto;
  column-gap: 27px;
  row-gap: 10px;
  max-width: 210px;

  @include vp-1023 {
    column-gap: 22px;
    max-width: 120px;
  }

  @include vp-767 {
    margin-left: 0;
    margin-top: -7px;
  }
}

.social__link {

  svg {
    fill: $color-alice-blue;

    @include vp-1023 {
      width: 16px;
      height: 16px;
    }
  }

  &--ok {

    svg {

      @include vp-1023 {
        width: 10px;
      }
    }
  }
}
